<template>
  <div class="container-fluid p-0">
    <div class="row">
      <div class="col-md-12">
        <h1 class="h1 mb-0">Data Pengajuan Saya</h1>
        <br />
      </div>
      <div class="col-md-12">
        <div class="card">
          <div class="card-body">
            <div class="table-responsive">
              <table class="table table-striped col-12">
                <thead>
                  <tr>
                    <th scope="col">No.</th>
                    <th scope="col">Kepada</th>
                    <th scope="col">Status</th>
                    <th scope="col">Alasan</th>
                    <th scope="col">Tanggal Pengajuan</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="(dtpengajuan, index) in getDataPengajuan"
                    :key="dtpengajuan.letter"
                  >
                    <td>{{ number(index) }}</td>
                    <td>{{ dtpengajuan.to }}</td>
                    <td class="check-status" v-if="dtpengajuan.status === 0">
                      <div>
                        <span class="badge badge-warning badge-lg"
                          >Menunggu</span
                        >
                      </div>
                    </td>
                    <td class="check-status" v-if="dtpengajuan.status === 1">
                      <div>
                        <span class="badge badge-success badge-lg"
                          >Diterima</span
                        >
                      </div>
                    </td>
                    <td class="check-status" v-if="dtpengajuan.status === 2">
                      <div>
                        <span class="badge badge-danger badge-lg">Ditolak</span>
                      </div>
                    </td>
                    <td>-</td>
                    <td>{{dtpengajuan.created_at | formatDate }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "PengajuanData",
  data() {
    return {
      dtpengajuan: [],
    };
  },
  computed: {
    ...mapGetters({
      getDataPengajuan: "riwayatpengajuan/getRiwayat",
    }),
  },
  methods: {
    ...mapActions({
      dtriwayat: "riwayatpengajuan/riwayatsuratjenis",
    }),
    getPengajuanSuratAll() {
      this.dtriwayat();
    },
    number: function (index) {
      return index + 1;
    },
  },
  mounted() {
    this.getPengajuanSuratAll();
    $("title").html("Riwayat Pengajuan Surat | STTBANDUNG");
  },
};
</script>

<style>
.check {
  color: green;
}
.times {
  color: red;
}
</style>